import { BrowserRouter, Route, Routes } from 'react-router-dom';
import FixTextReader from '../../../pages/FixTextReader/FixTextReader';
import JoinText from '../../../pages/JoinText/JoinText';
import Side from '../Side/Side';
import style from './Style.module.css';

interface MainGridProp {
  rootClassName?: string
}

function MainGrid({
  rootClassName
}: MainGridProp) {
  return (

    <BrowserRouter>
      <div className={rootClassName}>
        <div className={style.grid}>
          <div className={style.side}>
            <Side />
          </div>
          <div className={style.main}>

            <Routes>
              <Route path={`${process.env.PUBLIC_URL}/jointext`} element={<JoinText />} />
              <Route path={`${process.env.PUBLIC_URL}/fixtext`} element={<FixTextReader />} />
            </Routes>

          </div>
        </div>
      </div>
    </BrowserRouter>
  )
}

export default MainGrid